body {
  color: brown;
  background-color: bisque;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #e9bf9d;
  border-bottom-color: brown;
  border-radius: 50%;
  margin-top: 10px;
  animation: rotation 1s linear infinite;
  display: flex;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  cursor: pointer;
  color: beige;
  box-sizing: border-box;
  vertical-align: middle;
  height: 2.2em;
  box-shadow: inset 0 1px 2px rgba(219, 28, 28, .3), 0 1px 2px rgrgba(197, 35, 35, .4);
  background: #581313;
  border: 1px solid brown;
  border-radius: .3em;
  margin: 2px;
  padding: .3em .6em;
  font-family: Times New Roman, Times, serif;
  font-weight: 600;
  display: inline-block;
}

.breed-select::-webkit-scrollbar {
  display: none;
}

.breed-select:active, .breed-select:hover, .breed-select:focus {
  border: none;
}

.cat-info {
  visibility: hidden;
  --s: 10px;
  padding: var(--s);
  border: calc(2 * var(--s) ) solid rgba(28, 4, 4, 0);
  outline-offset: calc(-1 * var(--s) );
  background: conic-gradient(from 90deg at 1px 1px, rgba(51, 6, 6, 0) 25%, #1e0404 0);
  outline: 1px solid #2d0404;
  flex-direction: column;
  display: flex;
}

.cat-info img {
  border-radius: 10%;
  margin: auto;
  box-shadow: 0 1px 1px rgba(151, 103, 48, .11), 0 2px 2px rgba(0, 0, 0, .11), 0 4px 4px rgba(151, 0, 0, .11), 0 6px 8px rgba(151, 0, 0, .11), 0 8px 16px rgba(151, 0, 0, .11);
}

